<template>
    <div>
        <div class="col-md-3">
            <div class="list-group">
                <router-link class="list-group-item" :class="{'active' : $route.name === 'admin-update-android'}" :to="{name: 'admin-update-android'}">{{ langStore.language[36] }}</router-link>
                <router-link class="list-group-item" :class="{'active' : $route.name === 'admin-update-gui'}" :to="{name: 'admin-update-gui'}">{{ langStore.language[37] }}</router-link>
            </div>
        </div><!-- end .col-md-3 -->
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      langStore: state => state.langStore
    })
  }
}
</script>



